@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:ital@1&family=Poppins:wght@200;400&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

#box_title {
    display: flex;
    justify-content: center;
    width: 400px;
    font-size: 25px;
    align-items: center;
    color: #fff;
    margin-bottom: 0.75rem;
}

#box {
    width: 500px;
    display: flex;
    justify-content: center;
}

#creat_btn {
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    margin-bottom: 0.75rem;
}

table {
    outline: none;
    width: 400px;
    padding: 15px;
    font-size: 20px;
    text-align: center;
    color: black;
    background-color: #fff;
    border: 1px solid #64363C;
    border-radius: 20px;


}

th {
    background-color: hsl(49, 96%, 49%);
    padding: 10px;
    border: 1px solid #000;
    color: black;
}

td {
    border: 1px solid #000;
    padding: 5px;
    font-size: 20px;
}

.btn_img {
    display: flex;
    justify-content: center;
}

.container {
    background-color: hsl(219, 48%, 8%);
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.btn {
    font-size: 20px;
    text-align: center;
}

.btn:hover {
    transform: scale(1.2);
    transition: 0.4s;
}

#creat_input {
    border: 1px solid #fff;
    background-color: hsl(219, 48%, 8%);
    border-radius: 10px;
    width: 300px;
    height: 35px;
    padding: 10px;
    margin-right: 10px;
    margin-bottom: 0.5rem;
    color: #fff
}

.creat_box {
    border: 1px solid;
    background-color: rgb(250, 185, 66);
    border-radius: 30px;
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    margin-bottom: 0.5rem;
    cursor: pointer;
    font-size: 20px;
    font-weight: bolder;
}

.creat_box:hover {
    transform: scale(1.2);
    transition: 0.4s;
}

a {
    color: black;
    text-decoration: none;
}

.container_delete,
.container_edit {
    color: black;

}

#edit_box {
    height: 300px;
    width: 400px;
    height: 160px;
    border-radius: 30px;
    padding: 10px;
    background-color: #fff;
    text-align: center;
    align-items: center;
}


#Name {
    background: none;
    border-radius: 10px;
    background-color: #fff;
    width: 200px;
    height: 30px;
    margin: 0.5rem;
    text-align: center;
    font-size: 20px;
}

.btn {
    background-color: rgb(250, 185, 66);
    border: 1px solid;
    border-radius: 10px;
    padding: 3px;
    margin: 0.3rem;
    cursor: pointer;
}

.delete_title {
    font-size: 30px;
    font-weight: bolder;
    color: black;
}